// ---------- Validation ----------
const schema = z.object({
  customerName: z.string().min(2, "Please enter a name."),
  phone: z.string().optional().or(z.literal("")),
  service: z.string().min(1, "Select a service."),
  staff: z.string().optional().or(z.literal("")),
  date: z.date({ message: "Pick a date." }),
  time: z.string().min(1, "Pick a time."),
  notes: z
    .string()
    .max(500, "Max 500 characters.")
    .optional()
    .or(z.literal("")),
});

type FormValues = z.infer<typeof schema>;

// ---------- Local, per-section horizontal slider ----------
function LocalHSlider({
  children,
  ariaLabel,
  step = 360,
  className,
}: {
  children: React.ReactNode;
  ariaLabel: string;
  step?: number;
  className?: string;
}) {
  const ref = React.useRef<HTMLDivElement>(null);
  const [canLeft, setCanLeft] = React.useState(false);
  const [canRight, setCanRight] = React.useState(false);

  const update = React.useCallback(() => {
    const el = ref.current;
    if (!el) return;
    setCanLeft(el.scrollLeft > 0);
    setCanRight(el.scrollLeft + el.clientWidth < el.scrollWidth - 1);
  }, []);

  React.useEffect(() => {
    update();
    const el = ref.current;
    if (!el) return;
    const onResize = () => update();
    window.addEventListener("resize", onResize);
    el.addEventListener("scroll", update, { passive: true });
    return () => {
      window.removeEventListener("resize", onResize);
      el.removeEventListener("scroll", update as any);
    };
  }, [update]);

  // Keep wheel/touch horizontal inside the container
  const onWheel: React.WheelEventHandler<HTMLDivElement> = (e) => {
    const el = ref.current;
    if (!el) return;
    // convert vertical wheel into horizontal scroll
    if (Math.abs(e.deltaY) > Math.abs(e.deltaX)) {
      el.scrollBy({ left: e.deltaY, behavior: "auto" });
      e.preventDefault();
      e.stopPropagation();
    }
  };

  return (
    <div className={cn("relative", className)}>
      {/* Gradient edges */}
      <div className="pointer-events-none absolute inset-y-0 left-0 w-12 bg-gradient-to-r from-background to-transparent rounded-l-xl" />
      <div className="pointer-events-none absolute inset-y-0 right-0 w-12 bg-gradient-to-l from-background to-transparent rounded-r-xl" />

      {/* Left arrow */}
      <div className="absolute inset-y-0 left-1 flex items-center">
        <Button
          type="button"
          size="icon"
          variant="secondary"
          className="h-8 w-8 rounded-full shadow"
          disabled={!canLeft}
          onClick={() =>
            ref.current?.scrollBy({ left: -step, behavior: "smooth" })
          }
          aria-label="Scroll left"
        >
          ‹
        </Button>
      </div>

      {/* Right arrow */}
      <div className="absolute inset-y-0 right-1 flex items-center">
        <Button
          type="button"
          size="icon"
          variant="secondary"
          className="h-8 w-8 rounded-full shadow"
          disabled={!canRight}
          onClick={() =>
            ref.current?.scrollBy({ left: step, behavior: "smooth" })
          }
          aria-label="Scroll right"
        >
          ›
        </Button>
      </div>

      {/* Track */}
      <div
        ref={ref}
        role="region"
        aria-label={ariaLabel}
        onWheel={onWheel}
        className={cn(
          "overflow-x-auto overflow-y-hidden",
          "scroll-smooth snap-x snap-mandatory",
          "rounded-xl border px-1",
          // hide native scrollbars visually but keep accessible
          "[-ms-overflow-style:'none'] [scrollbar-width:'none'] [&::-webkit-scrollbar]:hidden"
        )}
        tabIndex={0}
      >
        <div className="flex w-max gap-4 py-4">{children}</div>
      </div>
    </div>
  );
}

// ---------- Cards ----------
function ServiceCard({
  selected,
  onSelect,
  service,
}: {
  selected: boolean;
  onSelect: () => void;
  service: Service;
}) {
  return (
    <div
      role="radio"
      aria-checked={selected}
      tabIndex={0}
      onClick={onSelect}
      onKeyDown={(e) => {
        if (e.key === "Enter" || e.key === " ") {
          e.preventDefault();
          onSelect();
        }
      }}
      className={cn(
        "w-[280px] sm:w-[340px] lg:w-[420px] shrink-0",
        "snap-start cursor-pointer rounded-lg border bg-card p-4",
        "flex items-stretch justify-between gap-4",
        selected
          ? "ring-2 ring-primary/70"
          : "ring-1 ring-transparent hover:ring-primary/30"
      )}
    >
      <div className="flex min-w-0 flex-col gap-1">
        <p className="text-sm text-muted-foreground">{service.group}</p>
        <p className="font-semibold truncate">{service.label}</p>
        <p className="text-sm text-muted-foreground">{service.price}</p>
        <div className="pt-2">
          <Button
            type="button"
            onClick={(e) => {
              e.stopPropagation();
              onSelect();
            }}
            variant={selected ? "default" : "secondary"}
            className={cn(
              "h-8 px-4 w-fit",
              !selected && "bg-accent text-foreground hover:bg-accent/80"
            )}
          >
            {selected ? "Selected" : "Select"}
          </Button>
        </div>
      </div>
      <div
        aria-label={`${service.label} image`}
        className="flex-1 w-full rounded-lg bg-cover bg-center aspect-video"
        style={{ backgroundImage: `url(${service.image})` }}
      />
    </div>
  );
}

function StaffCard({
  selected,
  onSelect,
  staff,
}: {
  selected: boolean;
  onSelect: () => void;
  staff: { id: string; label: string; role: string; avatar?: string };
}) {
  return (
    <div
      role="radio"
      aria-checked={selected}
      tabIndex={0}
      onClick={onSelect}
      onKeyDown={(e) => {
        if (e.key === "Enter" || e.key === " ") {
          e.preventDefault();
          onSelect();
        }
      }}
      className="w-[220px] sm:w-[260px] lg:w-[300px] shrink-0 snap-start rounded-lg border bg-card p-4 flex flex-col items-center gap-4 cursor-pointer"
    >
      <div
        className="h-24 w-24 rounded-full bg-center bg-cover"
        style={{
          backgroundImage: staff.avatar ? `url(${staff.avatar})` : undefined,
          backgroundColor: staff.avatar ? undefined : "oklch(0.92 0 0)",
        }}
      />
      <div className="text-center">
        <p className="font-semibold">{staff.label}</p>
        <p className="text-sm text-muted-foreground">{staff.role}</p>
      </div>
      <Button
        type="button"
        onClick={onSelect}
        variant={selected ? "default" : "secondary"}
        className={cn(
          "h-8 px-4 w-full",
          !selected && "bg-accent text-foreground hover:bg-accent/80"
        )}
      >
        {selected ? "Selected" : "Select"}
      </Button>
    </div>
  );
}

export default function NewAppointmentPage() {
  const [serviceQuery, setServiceQuery] = React.useState("");
  const [staffQuery, setStaffQuery] = React.useState("");

  const form = useForm<FormValues>({
    resolver: zodResolver(schema),
    defaultValues: {
      customerName: "",
      phone: "",
      service: "",
      staff: "",
      date: undefined as unknown as Date,
      time: "",
      notes: "",
    },
  });

  const selectedService = form.watch("service");
  const selectedStaff = form.watch("staff");
  const selectedTime = form.watch("time");

  const filteredServices = React.useMemo(() => {
    const q = serviceQuery.trim().toLowerCase();
    if (!q) return SERVICES;
    return SERVICES.filter(
      (s) =>
        s.label.toLowerCase().includes(q) || s.group.toLowerCase().includes(q)
    );
  }, [serviceQuery]);

  const filteredStaff = React.useMemo(() => {
    const q = staffQuery.trim().toLowerCase();
    if (!q) return STAFF;
    return STAFF.filter(
      (st) =>
        st.label.toLowerCase().includes(q) || st.role.toLowerCase().includes(q)
    );
  }, [staffQuery]);

  async function onSubmit(values: FormValues) {
    const payload = {
      ...values,
      date: values.date.toISOString().split("T")[0],
    };

    const res = await fetch("/api/appointments", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });

    if (!res.ok) {
      const j = await res.json().catch(() => null);
      alert(j?.message ?? "Failed to create appointment.");
      return;
    }
    const j = await res.json();
    alert(`Appointment created (id: ${j.id}).`);
    form.reset();
  }

  return (
    <div className="mx-auto w-full max-w-5xl p-6">
      {/* Page heading */}
      <div className="mb-6 px-1">
        <h1 className="text-4xl font-black tracking-tight">
          Create New Appointment
        </h1>
        <p className="text-muted-foreground">
          Pick a service, staff, a suitable date/time, then submit.
        </p>
      </div>

      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-12">
          {/* Step 1: Service Selection */}
          <section className="space-y-4">
            <h2 className="text-[22px] font-bold tracking-tight">
              1. Service Selection
            </h2>

            <div className="grid gap-3 rounded-xl border p-4">
              <Label htmlFor="service-search" className="sr-only">
                Search service
              </Label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
                <Input
                  id="service-search"
                  placeholder="Search service (e.g., Haircut)"
                  className="pl-9 h-12"
                  value={serviceQuery}
                  onChange={(e) => setServiceQuery(e.target.value)}
                />
              </div>

              <FormField
                control={form.control}
                name="service"
                render={() => (
                  <FormItem>
                    <LocalHSlider ariaLabel="Service slider">
                      {filteredServices.map((s) => (
                        <ServiceCard
                          key={s.id}
                          service={s}
                          selected={selectedService === s.id}
                          onSelect={() =>
                            form.setValue("service", s.id, {
                              shouldDirty: true,
                              shouldTouch: true,
                              shouldValidate: true,
                            })
                          }
                        />
                      ))}
                    </LocalHSlider>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
          </section>

          {/* Step 2: Staff Selection */}
          <section className="space-y-4">
            <h2 className="text-[22px] font-bold tracking-tight">
              2. Staff Selection
            </h2>

            <FormField
              control={form.control}
              name="staff"
              render={() => (
                <FormItem>
                  <div className="grid gap-3 rounded-xl border p-4">
                    <Label htmlFor="staff-search" className="sr-only">
                      Search staff
                    </Label>
                    <div className="relative">
                      <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
                      <Input
                        id="staff-search"
                        placeholder="Search staff (e.g., Ahmet, Barber)"
                        className="pl-9 h-12"
                        value={staffQuery}
                        onChange={(e) => setStaffQuery(e.target.value)}
                      />
                    </div>

                    <LocalHSlider ariaLabel="Staff slider">
                      {filteredStaff.map((st) => (
                        <StaffCard
                          key={st.id || "any"}
                          staff={st}
                          selected={selectedStaff === st.id}
                          onSelect={() =>
                            form.setValue("staff", st.id, {
                              shouldValidate: true,
                            })
                          }
                        />
                      ))}
                    </LocalHSlider>

                    <FormDescription>
                      Leave “No preference” to auto-assign the first available
                      staff.
                    </FormDescription>
                    <FormMessage />
                  </div>
                </FormItem>
              )}
            />
          </section>

          {/* Step 3: Date & Time */}
          <section className="space-y-4">
            <h2 className="text-[22px] font-bold tracking-tight">
              3. Date &amp; Time
            </h2>

            <div className="grid gap-6 rounded-xl border p-4 lg:grid-cols-2">
              {/* Calendar (shadcn) */}
              <FormField
                control={form.control}
                name="date"
                render={({ field }) => (
                  <FormItem className="flex flex-col">
                    <FormLabel>Date</FormLabel>
                    <Popover>
                      <PopoverTrigger asChild>
                        <FormControl>
                          <Button
                            variant="outline"
                            className={cn(
                              "w-full justify-start text-left font-normal",
                              !field.value && "text-muted-foreground"
                            )}
                          >
                            <CalendarIcon className="mr-2 h-4 w-4" />
                            {field.value
                              ? format(field.value, "PPP")
                              : "Pick a date"}
                          </Button>
                        </FormControl>
                      </PopoverTrigger>
                      <PopoverContent className="w-auto p-0" align="start">
                        <Calendar
                          mode="single"
                          selected={field.value}
                          onSelect={(d) => field.onChange(d ?? undefined)}
                          disabled={(d) =>
                            d < new Date(new Date().setHours(0, 0, 0, 0))
                          }
                          initialFocus
                        />
                      </PopoverContent>
                    </Popover>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {/* Time grid */}
              <FormField
                control={form.control}
                name="time"
                render={() => (
                  <FormItem>
                    <FormLabel>Available Times</FormLabel>
                    <div className="grid grid-cols-3 sm:grid-cols-4 gap-2">
                      {TIME_SLOTS.map((t) => {
                        const disabled = t === "10:30" || t === "15:45"; // example disabled
                        const active = selectedTime === t;
                        return (
                          <button
                            type="button"
                            key={t}
                            disabled={disabled}
                            onClick={() =>
                              form.setValue("time", t, { shouldValidate: true })
                            }
                            className={cn(
                              "rounded-lg border px-2 py-2 text-center text-sm transition",
                              disabled &&
                                "cursor-not-allowed bg-muted text-muted-foreground",
                              !disabled &&
                                "hover:border-primary hover:text-primary",
                              active &&
                                "border-primary bg-primary/20 text-primary"
                            )}
                          >
                            {t}
                          </button>
                        );
                      })}
                    </div>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
          </section>

          {/* Customer + Notes */}
          <section className="space-y-4">
            <h2 className="text-[22px] font-bold tracking-tight">
              4. Customer Details
            </h2>

            <div className="grid gap-4 md:grid-cols-2">
              <FormField
                control={form.control}
                name="customerName"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Customer Name</FormLabel>
                    <FormControl>
                      <Input placeholder="e.g., Alice Johnson" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="phone"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Phone (optional)</FormLabel>
                    <FormControl>
                      <Input placeholder="+90 5xx xxx xx xx" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <FormField
              control={form.control}
              name="notes"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Notes (optional)</FormLabel>
                  <FormControl>
                    <Textarea
                      rows={4}
                      placeholder="Any preferences or extra details…"
                      {...field}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </section>

          {/* Actions */}
          <div className="flex items-center justify-between">
            <Button
              type="button"
              variant="secondary"
              onClick={() => history.back()}
            >
              Back
            </Button>
            <Button type="submit">Confirm Appointment</Button>
          </div>
        </form>
      </Form>
    </div>
  );
}
